          </v-row>
          <v-card-actions class="mt-6">
            <v-spacer></v-spacer>
            <v-btn color="primary" text> Print </v-btn>
            <v-btn color="info" text :loading="loading" :disabled="loading">
              Export
            </v-btn>
          </v-card-actions>
        </v-card-text>
      </v-card>
    </div>
  </v-container>
</template>

<script>
export default {
  name: "InvoiceDetail",
  data() {
    return {
      loading: false,
      customerId: "",
      invoice: [],
      invoiceStatusColor: "",
      status: [
        {
          id: 1,
          name: "created",
        },
        {
          id: 2,
          name: "approved",
        },
        {
          id: 3,
          name: "to_confirm_payment",
        },
        {
          id: 4,
          name: "rejected",
        },
        {
          id: 5,
          name: "success",
        },
      ],
    };
  },
  methods: {
    backPrevios() {
      this.$router.go(-1);
    },
    fetchData() {
      this.$store.commit("Loading_State", true);
      this.$axios
        .get("invoice/" + this.$route.params.id)
        .then((res) => {
          if (res.data.code == 200) {
            setTimeout(() => {
              this.$store.commit("Loading_State", false);
              this.invoice = res.data.data;
              console.log(this.invoice);
            }, 100);
          }
        })
        .catch((error) => {
          this.$store.commit("Loading_State", false);
          this.fetchData();
          if (error.response.status == 422) {
            var obj = error.response.data.errors;
            for (let [key, message] of Object.entries(obj)) {
              this.server_errors[key] = message[0];
            }
          }
        });
    },
    invoiceStatus(data) {
      if (data == "created") {
        this.invoiceStatusColor = "primary--text";
        return "ສ້າງບິນສຳເລັດ";
      } else if (data == "approved") {
        this.invoiceStatusColor = "info--text";
        return "ອະນຸມັດແລ້ວ";
      } else if (data == "to_confirm_payment") {
        this.invoiceStatusColor = "warning--text";
        return "ລໍຖ້າຢືນຢັນການຊຳລະ";
      } else if (data == "rejected") {
        this.invoiceStatusColor = "error--text";
        return "ຊຳລະບໍ່ສຳເລັດ";
      } else if (data == "success") {
        this.invoiceStatusColor = "success--text";
        return "ສຳເລັດການຊຳລະ";
      }
    },
    //     invoiceStatusColor(data) {
    //   if (data == "success") {
    //     return "success";
    //   }
    // },

    editPage(id) {
      this.$router.push({
        name: "EditCustomer",
        params: { id },
      });
    },
  },
  watch: {},
  created() {
    this.fetchData();
  },
};
</script>

